import React, { useState } from 'react';
import styled from 'styled-components';
import { ReactComponent as SettingsIcon } from './media/settings.svg';

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const Title = styled.h1`
  font-size: 3rem;
  @media (max-width: 576px) {
    font-size: 2.3rem;
  }
`;

const SubTitle = styled.div`
  p {
    display: inline-block;
  }
`;

const Input = styled.input`
  border: #fff 1px solid;
  border-radius: 12px;
  padding: 15px;
  color: #fff;
  background-color: #3d3d3d;
  font-size: 30px;
  @media (max-width: 576px) {
    font-size: 18px;
  }
  ::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.7);
  }
  :-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.7);
  }
  ::placeholder {
    color: rgba(255, 255, 255, 0.7);
  }
`;

const Table = styled.table`
  overflow: scroll;
  margin-top: 15px;
  border-collapse: collapse;
  text-align: center;
  vertical-align: center;
  td {
    padding: 15px;
  }
`;

const Footer = styled.footer`
  position: relative;
  div {
    position: fixed;
    bottom: 20px;
    right: 20px;
    cursor: pointer;
    svg {
      fill: #fff;
      height: 45px;
    }
  }
`;

const App = () => {
  const [input, setInput] = useState('');
  const [total, setTotal] = useState({
    withTax: parseFloat(0).toFixed(2),
    withoutTax: parseFloat(0).toFixed(2),
    tax: parseFloat(0).toFixed(2)
  });

  const calcBill = e => {
    const { value } = e.target;
    if (!isNaN(value)) {
      const taxPercentage = 17;
      const KWPH = 0.4726;
      const withoutTax = value * KWPH;
      const tax = (withoutTax * taxPercentage) / 100;
      const withTax = parseFloat(withoutTax) + parseFloat(tax);
      setInput(value);
      setTotal({
        withoutTax: parseFloat(withoutTax).toFixed(2),
        tax: parseFloat(tax).toFixed(2),
        withTax: parseFloat(withTax).toFixed(2)
      });
    }
  };

  return (
    <>
      <Container>
        <header>
          <Title>מחשבון קוט"ש</Title>
          <SubTitle>
            <p>מחיר קוט"ש - 47.26 אג'</p>
            &nbsp;&nbsp;&#124;&nbsp;&nbsp;
            <p>מע"מ - 17%</p>
          </SubTitle>
        </header>
        <Input type="text" value={input} onChange={calcBill} placeholder={'קוט"ש'} />
        <Table>
          <tbody>
            <tr>
              <td>ללא מע"מ:</td>
              <td>{total.withoutTax}</td>
            </tr>
            <tr>
              <td>מע"מ:</td>
              <td>{total.tax}</td>
            </tr>
            <tr>
              <td>סה"כ:</td>
              <td>{total.withTax}</td>
            </tr>
          </tbody>
        </Table>
      </Container>
      <Footer>
        <div>
          <SettingsIcon />
        </div>
      </Footer>
    </>
  );
};

export default App;
